#form {
    position: absolute;
    top:0%;
    left:0%;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
    height: 100vh;
    width: 100%;
}

#form .form-wrapper{
    display: inline-block;
    border-radius: 0;
    z-index: 999;
    position: relative;
    width: 100%;
    background-color: #625AC0;
    height: 100%;
}
#form .form-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
}
.form-rights {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
