.icon_ani {
    -webkit-animation: shake 0.75s;
            animation: shake 0.75s; 
    -webkit-animation-iteration-count: 1; 
            animation-iteration-count: 1;
}

@-webkit-keyframes shake {
    0%, 20%, 40%, 60%, 80% {
      -webkit-transform: translateX(4px);
              transform: translateX(4px);
    }
    10%,
    30%,
    50%,
    70%,
    90% {
      -webkit-transform: translateX(-4px);
              transform: translateX(-4px);
    }
  }

@keyframes shake {
    0%, 20%, 40%, 60%, 80% {
      -webkit-transform: translateX(4px);
              transform: translateX(4px);
    }
    10%,
    30%,
    50%,
    70%,
    90% {
      -webkit-transform: translateX(-4px);
              transform: translateX(-4px);
    }
  }
  
