.click-profile {
    position: absolute;
    right: 20px;
    margin-top: .7rem;
    background-color: #fefefe;
    box-shadow: 0px 0px 10px rgba(224, 224, 224, 0.44);
    padding: 12px 10px;
    border-radius: 10px;
    -webkit-animation: scale .2s ease;
            animation: scale .2s ease;
    z-index: 1000;
}
@-webkit-keyframes scale {
    0% {
        -webkit-transform: scale(0.3);
                transform: scale(0.3);
        opacity: 0;
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}
@keyframes scale {
    0% {
        -webkit-transform: scale(0.3);
                transform: scale(0.3);
        opacity: 0;
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}
